.header {
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
}

.header-bg {
  position: absolute;
  width: 100%;
  height: 100%;
  background-image: url(../img/header-bg-light.jpg);
  background-size: 100% 100%;
}

.rhombus {
  position: absolute;
  transform: translate(calc(50vw - 50%), calc(50vh - 50%)) rotate(225deg)
    scale(1);
}

